.menu {
    display: none;

    &--open {
        display: flex;
        flex-direction: column-reverse;
        width: 100vw;
        position: absolute;
        top: 80px;
        right: -20px;
        background-color: #f8f8f8;
        z-index: 2;
    }
}

.menu__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    max-width: 100%;
    padding: 20px 0;
    border-top: 1px solid #e5e5e5;
    position: relative;

    &:hover .menu__link {
        color: #00c8c8;
    }

    &:nth-child(4):hover .menu__link {
        margin-bottom: 13px;
    }

    &:nth-child(4):hover .sub-menu  {
        display: block;
    }
}

.menu__link {
    display: block;
    color: #222222;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 10px;
    font-weight: 500;
}

.menu__form {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 0;
    border-top: 1px solid #e5e5e5;
}

@media (min-width: @tablet-width) {
    .menu {
        display: flex;
        height: 100%;
    }

    .menu__list {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        width: 341px;
    }
    
    .menu__item  {
       border: none; 
       max-width: 341px;

       &:not(:last-child) {
        margin-right: 14px;
       }

       &:nth-child(4):hover .menu__link {
        margin-bottom: 0;
    }
    }

    .menu__form {
        margin-left: 20px;
    }
}

@media(min-width: @desktop-width) {
    .cities {
        width: 1170px;
    }

    .menu__link {
        font-size: 12px;
    }

    .menu__item  {
        &:not(:last-child) {
         margin-right: 57px;
        }
    }

    .menu__list {
        width: 595px;
    }

    .menu__form {
        margin-left: 57px;
    }
}

@media(min-width: 1440px) {
    .menu__form {
        margin-left: 115px;
    }
}