@font-face {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  src: url("../../fonts/Montserrat-Light.woff2") format("woff2"), url("../../fonts/Montserrat-Light.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/Montserrat-Regular.woff2") format("woff2"), url("../../fonts/Montserrat-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/Montserrat-Bold.woff2") format("woff2"), url("../../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/Roboto-Regular.woff2") format("woff2"), url("../../fonts/Roboto-Regular.woff") format("woff");
  font-display: swap;
}
html,
body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat";
  font-weight: 400;
}
.page-header__main-block {
  min-height: 80px;
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  background-color: #f8f8f8;
}
.page-header__logo-text {
  text-transform: uppercase;
  font-size: 18px;
}
.page-header__logo-bold {
  font-weight: 700;
}
.page-header__logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #222222;
}
.page-header__wrapper-navigation {
  display: flex;
  align-items: center;
  position: relative;
}
@media (min-width: 768px) {
  .page-header__button-burger {
    display: none;
  }
}
@media (min-width: 1200px) {
  .page-header__main-block {
    padding: 0 34px;
  }
}
@media (min-width: 1440px) {
  .page-header__main-block {
    padding: 0 134px;
  }
}
.top-bar {
  background-color: #333333;
  min-height: 45px;
}
.top-bar__wrapper {
  display: flex;
  justify-content: space-between;
  min-height: 45px;
  align-items: center;
}
.top-bar__wrapper-select {
  margin-left: 20px;
  display: flex;
  align-items: center;
}
.top-bar__currency {
  font-size: 12px;
  font-weight: 500;
  color: #777777;
}
.top-bar__select {
  background-color: transparent;
  border: none;
  font-size: 12px;
  font-weight: 500;
  color: #777777;
}
@media (min-width: 1200px) {
  .top-bar__wrapper {
    width: 1170px;
    margin: 0 auto;
  }
  .top-bar__wrapper-select {
    margin-left: 0;
  }
}
.user-account {
  min-height: 45px;
}
.user-account__list {
  display: flex;
  min-height: 45px;
}
.user-account__item:not(:last-child) {
  margin-right: 15px;
  padding-top: 10px;
}
.user-account__link {
  font-size: 12px;
  color: #858585;
  text-decoration: none;
}
.user-account__basket {
  width: 45px;
  height: 100%;
  padding: 0;
  background-color: #00c8c8;
  border: none;
  cursor: pointer;
}
.user-account__basket span {
  display: none;
}
.user-account__basket-arrow {
  display: none;
}
@media (min-width: 768px) {
  .user-account__basket {
    width: 147px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .user-account__basket span {
    display: block;
    color: #ffffff;
    font-size: 12px;
  }
  .user-account__basket-icon {
    margin-right: 22px;
  }
  .user-account__basket-arrow {
    display: block;
    margin-right: 0;
  }
  .user-account__item:last-child {
    margin-left: 40px;
  }
}
@media (min-width: 1200px) {
  .user-account__item:last-child {
    margin-left: 83px;
  }
}
.list-reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.menu {
  display: none;
}
.menu--open {
  display: flex;
  flex-direction: column-reverse;
  width: 100vw;
  position: absolute;
  top: 80px;
  right: -20px;
  background-color: #f8f8f8;
  z-index: 2;
}
.menu__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  max-width: 100%;
  padding: 20px 0;
  border-top: 1px solid #e5e5e5;
  position: relative;
}
.menu__item:hover .menu__link {
  color: #00c8c8;
}
.menu__item:nth-child(4):hover .menu__link {
  margin-bottom: 13px;
}
.menu__item:nth-child(4):hover .sub-menu {
  display: block;
}
.menu__link {
  display: block;
  color: #222222;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 10px;
  font-weight: 500;
}
.menu__form {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  border-top: 1px solid #e5e5e5;
}
@media (min-width: 768px) {
  .menu {
    display: flex;
    height: 100%;
  }
  .menu__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 341px;
  }
  .menu__item {
    border: none;
    max-width: 341px;
  }
  .menu__item:not(:last-child) {
    margin-right: 14px;
  }
  .menu__item:nth-child(4):hover .menu__link {
    margin-bottom: 0;
  }
  .menu__form {
    margin-left: 20px;
  }
}
@media (min-width: 1200px) {
  .cities {
    width: 1170px;
  }
  .menu__link {
    font-size: 12px;
  }
  .menu__item:not(:last-child) {
    margin-right: 57px;
  }
  .menu__list {
    width: 595px;
  }
  .menu__form {
    margin-left: 57px;
  }
}
@media (min-width: 1440px) {
  .menu__form {
    margin-left: 115px;
  }
}
.button-menu {
  background-color: transparent;
  border: none;
  width: 30px;
  height: 30px;
  padding: 0;
  background-image: url("../image/burger-menu.svg");
  display: flex;
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;
}
.button-menu--open {
  background-image: url("../image/burger-close.svg");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.form-search__input {
  border: none;
  background-color: #f8f8f8;
  border-top: 1px solid #e7e7e7;
  border-left: 1px solid #e7e7e7;
  border-bottom: 1px solid #e7e7e7;
  color: #989898;
  height: 10px;
  padding: 9.2px;
}
.form-search__button {
  padding: 0;
  width: 30px;
  height: 30px;
  padding: 6.7px 7.1px;
  border: none;
  background-color: transparent;
  border-top: 1px solid #e7e7e7;
  border-right: 1px solid #e7e7e7;
  border-bottom: 1px solid #e7e7e7;
  cursor: pointer;
}
@media (min-width: 768px) {
  .form-search__input {
    width: 130px;
  }
}
@media (min-width: 1200px) {
  .form-search__input {
    width: 140.8px;
  }
}
.banner {
  min-height: 450px;
  background-image: url("../image/banner.jpg");
  background-position: 50% 50%;
  margin-bottom: 32px;
}
.banner__shadow {
  width: 100%;
  min-height: 450px;
  background-color: #333333;
  opacity: 75%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.banner__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 2;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.banner__text h1 {
  font-weight: 700;
  font-size: 36px;
  color: #ffffff;
  margin: 0;
  margin-bottom: 54px;
}
.banner__text h1 span {
  font-weight: 300;
}
.banner__slogan {
  font-weight: 400;
  font-size: 20px;
  color: #ffffff;
}
.banner__background-text {
  display: none;
}
@media (min-width: 768px) {
  .banner__slogan {
    font-size: 23px;
  }
}
@media (min-width: 1200px) {
  .banner {
    margin-bottom: 62px;
  }
  .banner__background-text {
    display: block;
    font-weight: 700;
    font-size: 450px;
    color: #3e3e3e;
    position: absolute;
    top: 13px;
    letter-spacing: 70px;
  }
}
.cities {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 62px;
}
.cities__item:not(:last-child) {
  margin-bottom: 30px;
}
.cities__item h3 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 15px;
  font-weight: 500;
  margin: 0;
  margin-bottom: 30px;
  text-transform: uppercase;
  color: #575153;
}
.cities__item address {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 10px;
  text-transform: uppercase;
  font-style: normal;
  color: #727272;
  margin-bottom: 20px;
}
.cities__description {
  margin: 0;
  margin-bottom: 30px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 12px;
  color: #a5a3a3;
}
.cities__button {
  text-transform: uppercase;
  padding: 24px 34px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 4.97px;
  color: #454647;
  background-color: transparent;
  border: 2px solid #737373;
}
.cities__button:hover {
  background-color: #00c8c8;
  color: #f8f8f8;
  border-color: #00c8c8;
}
@media (min-width: 768px) {
  .cities {
    width: 600px;
  }
  .cities__list {
    display: grid;
    grid-template-columns: 280px 280px;
    row-gap: 40px;
    column-gap: 40px;
  }
}
@media (min-width: 1200px) {
  .cities {
    width: 1170px;
  }
  .cities__list {
    grid-template-columns: 368px 368px 368px;
    column-gap: 33px;
  }
}
.map {
  width: 100%;
  min-height: 890px;
  margin-bottom: 30px;
  display: none;
}
.map__image {
  width: 100%;
  height: 360px;
  background-position: 49% 50%;
  background-repeat: no-repeat;
}
.map__london .map__image {
  background-image: url("../image/map.jpg");
}
.map__new-york .map__image {
  background-image: url("../image/map.jpg");
}
.map__paris .map__image {
  background-image: url("../image/map.jpg");
}
.map__image::before {
  content: url("../image/geo.svg");
  display: flex;
  justify-content: center;
  width: 20px;
  height: 32px;
  margin: 0 auto;
  padding: 0;
  position: relative;
  top: 145px;
  z-index: 2;
}
.map__item {
  display: none;
}
.map__item--open {
  display: block;
}
@media (min-width: 768px) {
  .map {
    min-height: 530px;
    margin-bottom: 125px;
  }
  .map__image {
    background-position: 50% 50%;
  }
  .map__image::before {
    top: 234px;
  }
  .map__item--open {
    display: flex;
  }
  .map__image {
    height: 530px;
  }
}
.map-card {
  padding: 0 20px;
  background-color: #f8f8f8;
  padding-bottom: 30px;
  padding-top: 43px;
}
.map-card h3 {
  font-size: 13px;
  text-transform: uppercase;
  color: #575153;
  margin: 0;
  margin-bottom: 15px;
  font-weight: 500;
}
.map-card__place {
  display: block;
  font-size: 10px;
  text-transform: uppercase;
  color: #575153;
  font-weight: 500;
  margin-bottom: 21px;
}
.map-card__description {
  display: block;
  font-size: 11px;
  color: #969595;
  font-weight: 400;
  margin: 0;
  margin-bottom: 33px;
}
.map-card__links {
  margin-bottom: 65px;
}
.map-card__address {
  display: block;
  font-size: 11px;
  font-style: normal;
  color: #969595;
  margin-bottom: 21px;
}
.map-card__telephone {
  display: block;
  font-size: 11px;
  color: #969595;
  margin-bottom: 21px;
  text-decoration: none;
}
.map-card__site {
  display: block;
  font-size: 11px;
  color: #969595;
  margin-bottom: 21px;
  text-decoration: none;
}
.map-card__mail {
  display: block;
  font-size: 11px;
  color: #969595;
  margin-bottom: 21px;
}
.map-card__timetable {
  display: block;
  font-size: 11px;
  color: #969595;
}
.map-card__list {
  display: flex;
}
.map-card__item {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #727272;
}
.map-card__item:not(:last-child) {
  margin-right: 14px;
}
.map-card__item:first-child svg {
  margin-top: -8px;
}
.map-card__item:hover {
  background-color: #575757;
}
.map-card__link {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
}
.map-card__address,
.map-card__telephone,
.map-card__site,
.map-card__mail,
.map-card__timetable {
  display: flex;
  align-items: center;
}
.map-card__site svg {
  transform: rotate(90deg);
}
.map-card__links svg {
  display: block;
  margin-right: 10px;
}
.map-card__links a:hover svg,
.map-card__links a:hover path {
  fill: #575757;
}
.map-card__links a:hover span {
  color: #575757;
}
@media (min-width: 768px) {
  .map-card {
    width: 450px;
  }
  .map-card h3 {
    margin-bottom: 30px;
  }
}
@media (min-width: 1200px) {
  .map-card {
    width: 663px;
  }
}
.page-footer {
  background-color: #f8f8f8;
}
.page-footer__container {
  width: 280px;
  margin: 0 auto;
}
.page-footer__links {
  padding-top: 64px;
}
.page-footer__links a {
  font-size: 11px;
  text-decoration: none;
  color: #969696;
}
.page-footer__links h2 {
  font-size: 11px;
  color: #777274;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 34px;
}
.page-footer__links li {
  font-size: 11px;
}
.page-footer__links li:not(:last-child) {
  margin-bottom: 19px;
}
.page-footer__links li:hover a {
  color: #42d5d5;
}
.page-footer__links section {
  margin-bottom: 60px;
}
.page-footer__contact address {
  font-size: 11px;
  color: #777274;
  font-style: normal;
  margin-bottom: 29px;
}
.page-footer__contact a {
  display: block;
  margin-bottom: 19px;
}
.page-footer__contact a:hover {
  color: #42d5d5;
}
.page-footer__contact span.page-footer__contact-email {
  font-size: 11px;
  color: #777274;
}
@media (min-width: 768px) {
  .page-footer__container {
    width: 700px;
  }
  .page-footer__links {
    display: grid;
    grid-template-columns: 210px 210px 210px;
    column-gap: 35px;
    row-gap: 35px;
    margin-bottom: 65px;
  }
  .page-footer__links section {
    margin-bottom: 0;
  }
}
@media (min-width: 1200px) {
  .page-footer__container {
    width: 1170px;
  }
  .page-footer__links {
    grid-template-columns: 130px 135px 137px 148px 223px;
    column-gap: 99px;
  }
  .page-footer__links section {
    margin-bottom: 0;
  }
}
.social {
  background-color: #00c8c8;
  padding: 83px 50px 84px;
}
.social__list {
  display: flex;
  justify-content: space-between;
  width: 180px;
}
.social__item {
  display: block;
  width: 32px;
  height: 32px;
}
.social__item:hover svg {
  fill: #e4e4e4;
}
.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
}
.social__link svg {
  display: block;
}
@media (min-width: 768px) {
  .social {
    width: 230px;
  }
  .social__list {
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .social {
    width: 282px;
    padding: 83px 145px 84px;
  }
  .social__list {
    width: 282px;
  }
}
.banners {
  padding-bottom: 56px;
}
.banners__award {
  background-color: #333333;
  padding: 62px 30px 64px;
  font-size: 18px;
  color: #ffffff;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .banners {
    display: flex;
    justify-content: space-between;
  }
  .banners__award {
    width: 270px;
    font-size: 22px;
    margin-bottom: 0;
  }
}
@media (min-width: 1200px) {
  .banners__award {
    width: 453px;
    padding: 62px 58px 64px;
    font-size: 26px;
    margin-bottom: 0;
  }
}
.copyright {
  min-height: 45px;
  background-color: #333333;
  padding: 16px 0;
}
.copyright span {
  display: block;
  font-size: 10px;
  color: #777777;
  font-weight: 500;
}
.copyright span:not(:last-child) {
  margin-bottom: 10px;
}
.copyright__container {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .copyright {
    min-height: 13px;
  }
  .copyright span:not(:last-child) {
    margin-bottom: 0;
  }
  .copyright__container {
    display: flex;
    justify-content: space-between;
    width: 700px;
  }
  .copyright__authors {
    display: flex;
    justify-content: space-between;
  }
  .copyright__authors span:first-child {
    margin-right: 37px;
  }
}
@media (min-width: 1200px) {
  .copyright__container {
    width: 1170px;
  }
}
.sub-menu {
  display: none;
  width: calc(100% - 40px);
}
.sub-menu__wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 20px;
}
.sub-menu__wrapper h2 {
  font-size: 11px;
  color: #777274;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 24px;
}
.sub-menu__list {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.sub-menu__link {
  display: block;
  font-size: 11px;
  text-decoration: none;
  color: #969696;
}
.sub-menu__item:not(:last-child) {
  margin-bottom: 19px;
}
.sub-menu__item:hover .sub-menu__link {
  color: #42d5d5;
}
.sub-menu__banner {
  width: 100%;
  min-height: 100px;
  background-color: #333333;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #ffffff;
}
@media (min-width: 768px) {
  .sub-menu {
    min-width: 306px;
    padding: 31px;
    position: absolute;
    top: 60px;
    z-index: 3;
    background-color: #f8f8f8;
    box-shadow: -5px 20px 30px #77727449;
    display: none;
  }
  .sub-menu__lists {
    display: grid;
    grid-template-columns: 133px 133px;
    column-gap: 40px;
    margin-bottom: 63px;
  }
  .sub-menu__wrapper {
    display: block;
  }
  .sub-menu__wrapper h2 {
    font-size: 11px;
    color: #777274;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 34px;
  }
  .sub-menu__list {
    display: block;
  }
  .sub-menu__banner {
    width: 100%;
    min-height: 150px;
    font-size: 26px;
  }
  .sub-menu__banner strong {
    display: block;
  }
  .sub-menu__banner span {
    display: block;
    font-weight: 500;
  }
}
@media (min-width: 1200px) {
  .sub-menu {
    min-width: 406px;
  }
  .sub-menu__lists {
    grid-template-columns: 164px 164px;
    column-gap: 80px;
    margin-bottom: 63px;
  }
  .sub-menu__banner {
    width: 100%;
    min-height: 200px;
    font-size: 34px;
  }
}
