.cities {
    width: 280px;
    margin: 0 auto;
    margin-bottom: 62px;
}

.cities__item {
    &:not(:last-child) {
        margin-bottom: 30px;
    }

    & h3 {
        font-family: @main-font;
        font-size: 15px;
        font-weight: 500;
        margin: 0;
        margin-bottom: 30px;
        text-transform: uppercase;
        color: #575153;
    }

    & address {
        font-family: @main-font;
        font-size: 10px;
        text-transform: uppercase;
        font-style: normal;
        color: #727272;
        margin-bottom: 20px;
    }
}

.cities__description {
    margin: 0;
    margin-bottom: 30px;
    font-family: @main-font;
    font-size: 12px;
    color: #a5a3a3;
}

.cities__button {
    text-transform: uppercase;
    padding: 24px 34px;
    font-family: @main-font;
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 4.97px;
    color: #454647;
    background-color: transparent;
    border: 2px solid #737373;

    &:hover {
        background-color: #00c8c8;
        color: #f8f8f8;
        border-color: #00c8c8;
    }
}

@media (min-width: @tablet-width) {
    .cities {
        width: 600px;
    }

    .cities__list {
        display: grid;
        grid-template-columns: 280px 280px;
        row-gap: 40px;
        column-gap: 40px;
    }
}


@media(min-width: @desktop-width) {
    .cities {
        width: 1170px;
    }

    .cities__list {
        grid-template-columns: 368px 368px 368px;
        column-gap: 33px;
    }
}