.sub-menu {
    display: none;
    width: calc(100% - 40px);
}

.sub-menu__wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 20px;

    & h2 {
        font-size: 11px;
        color: #777274;
        text-transform: uppercase;
        margin: 0;
        margin-bottom: 24px;
    }
}

.sub-menu__list {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sub-menu__link {
    display: block;
    font-size: 11px;
    text-decoration: none;
    color: #969696;
}

.sub-menu__item {
    &:not(:last-child) {
        margin-bottom: 19px;
    }

    &:hover .sub-menu__link {
        color: #42d5d5;
    }
}

.sub-menu__banner {
    width: 100%;
    min-height: 100px;
    background-color: #333333;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #ffffff;
}


@media (min-width: @tablet-width) {
    .sub-menu {
        min-width: 306px;
        padding: 31px;
        position: absolute;
        top: 60px;
        z-index: 3;
        background-color: #f8f8f8;
        box-shadow: -5px 20px 30px #77727449;
        display: none;
    }

    .sub-menu__lists {
        display: grid;
        grid-template-columns: 133px 133px;
        column-gap: 40px;
        margin-bottom: 63px;
    }

    .sub-menu__wrapper {
        display: block;

        & h2 {
            font-size: 11px;
            color: #777274;
            text-transform: uppercase;
            margin: 0;
            margin-bottom: 34px;
        }
    }

    .sub-menu__list {
        display: block;
    }


    .sub-menu__banner {
        width: 100%;
        min-height: 150px;
        font-size: 26px;

        & strong {
            display: block;
        }

        & span {
            display: block;
            font-weight: 500;
        }
    }
}


@media (min-width: @desktop-width) {
    .sub-menu {
        min-width: 406px;
    }

    .sub-menu__lists {
        grid-template-columns: 164px 164px;
        column-gap: 80px;
        margin-bottom: 63px;
    }

    .sub-menu__banner {
        width: 100%;
        min-height: 200px;
        font-size: 34px;
    }
}