.copyright {
    min-height: 45px;
    background-color: #333333;
    padding: 16px 0;

    & span {
        display: block;
        font-size: 10px;
        color: #777777;
        font-weight: 500;
        &:not(:last-child){
            margin-bottom: 10px;
        }
    }
}

.copyright__container {
    width: 280px;
    margin: 0 auto;
}

@media(min-width: @tablet-width) {
    .copyright {
        min-height: 13px;

        & span:not(:last-child) {
            margin-bottom: 0;
        }
    }

    .copyright__container {
        display: flex;
        justify-content: space-between;
        width: 700px;
    }

    .copyright__authors {
        display: flex;
        justify-content: space-between;

        & span:first-child {
            margin-right: 37px;
        }
    }
}

@media(min-width: @desktop-width) {
    .copyright__container {
        width: 1170px;
    }
}