.user-account {
    min-height: 45px;
}

.user-account__list {
    display: flex;
    min-height: 45px;
}

.user-account__item {
    &:not(:last-child){
        margin-right: 15px;
        padding-top: 10px;
    }
}

.user-account__link {
    font-size: 12px;
    color: #858585;
    text-decoration: none;
}

.user-account__basket {
    width: 45px;
    height: 100%;
    padding: 0;
    background-color: #00c8c8;
    border: none;
    cursor: pointer;
    & span {
        display: none;
    }
}

.user-account__basket-arrow {
    display: none;
}

@media (min-width: @tablet-width) {
    .user-account__basket {
        width: 147px;
        display: flex;
        align-items: center;
        justify-content: center;

        & span {
            display: block;
            color: #ffffff;
            font-size: 12px;
        }
    }

    .user-account__basket-icon {
        margin-right: 22px;
    }

    .user-account__basket-arrow {
        display: block;
        margin-right: 0;
    }

    .user-account__item {
        &:last-child{
            margin-left: 40px;
        }
    }
}

@media(min-width: @desktop-width) {
    .user-account__item {
        &:last-child{
            margin-left: 83px;
        }
    }
}