.map {
    width: 100%;
    min-height: 890px;
    margin-bottom: 30px;
    display: none;
}

.map__image {
    width: 100%;
    height: 360px;
    background-position: 49% 50%;
    background-repeat: no-repeat;
}

.map__london .map__image {
    background-image: url("../image/map.jpg");
}

.map__new-york .map__image {
    background-image: url("../image/map.jpg");
}

.map__paris .map__image {
    background-image: url("../image/map.jpg");
}

.map__image::before {
    content: url("../image/geo.svg");
    display: flex;
    justify-content: center;
    width: 20px;
    height: 32px;
    margin: 0 auto;
    padding: 0;
    position: relative;
    top: 145px;
    z-index: 2;
}

.map__item {
    display: none;
}

.map__item {
    &--open {
        display: block;
    }
}

@media (min-width: @tablet-width) {
    .map {
        min-height: 530px;
        margin-bottom: 125px;
    }

    .map__image {
        background-position: 50% 50%;
        &::before {
            top: 234px;
        }
    }

    .map__item {
        &--open {
            display: flex;
        }
    }

    .map__image {
        height: 530px;
    }
}
