.social {
    background-color: #00c8c8;
    padding: 83px 50px 84px;
}

.social__list {
    display: flex;
    justify-content: space-between;
    width: 180px;
}

.social__item {
    display: block;
    width: 32px;
    height: 32px;

    &:hover svg {
        fill: #e4e4e4;
    }
}

.social__link {
    display: flex;
    justify-content: center;
    align-items: center;

    & svg {
        display: block;
    }
}

@media(min-width: @tablet-width) {
    .social {
        width: 230px;
    }

    .social__list {
        width: 100%;
    }
}

@media(min-width: @desktop-width) {
    .social {
        width: 282px;
        padding: 83px 145px 84px;
    }

    .social__list {
        width: 282px;
    }
}