.map-card {
    padding: 0 20px;
    background-color: #f8f8f8;
    padding-bottom: 30px;
    padding-top: 43px;

    & h3 {
        font-size: 13px;
        text-transform: uppercase;
        color: #575153;
        margin: 0;
        margin-bottom: 15px;
        font-weight: 500;
    }
}

.map-card__place {
    display: block;
    font-size: 10px;
    text-transform: uppercase;
    color: #575153;
    font-weight: 500;
    margin-bottom: 21px;
}

.map-card__description {
    display: block;
    font-size: 11px;
    color: #969595;
    font-weight: 400;
    margin: 0;
    margin-bottom: 33px;
}

.map-card__links {
    margin-bottom: 65px;
}

.map-card__address {
    display: block;
    font-size: 11px;
    font-style: normal;
    color: #969595;
    margin-bottom: 21px;
}

.map-card__telephone{
    display: block;
    font-size: 11px;
    color: #969595;
    margin-bottom: 21px;
    text-decoration: none;
}

.map-card__site {
    display: block;
    font-size: 11px;
    color: #969595;
    margin-bottom: 21px;
    text-decoration: none;
}

.map-card__mail {
    display: block;
    font-size: 11px;
    color: #969595;
    margin-bottom: 21px;
}

.map-card__timetable {
    display: block;
    font-size: 11px;
    color: #969595;
}

.map-card__list {
    display: flex;
}

.map-card__item {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #727272;

    &:not(:last-child) {
        margin-right: 14px;
    }

    &:first-child svg {
        margin-top: -8px;
    }

    &:hover {
        background-color: #575757;
    }
}

.map-card__link {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
}

.map-card__address, .map-card__telephone, .map-card__site, .map-card__mail, .map-card__timetable {
    display: flex;
    align-items: center;
}

.map-card__site {
    & svg {
        transform: rotate(90deg);
    }
}

.map-card__links {
    & svg {
        display: block;
        margin-right: 10px;
    }

    & a:hover {
        & svg, path {
            fill: #575757;
        }

        & span {
            color: #575757;
        }
    }
}


@media (min-width: @tablet-width) {
    .map-card {
        width: 450px;
        & h3 {
            margin-bottom: 30px;
        }
    }
}


@media (min-width: @desktop-width) {
    .map-card {
        width: 663px;
    }
}