.button-menu {
    background-color: transparent;
    border: none;
    width: 30px;
    height: 30px;
    padding: 0;
    background-image: url("../image/burger-menu.svg");
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
}

.button-menu--open {
    background-image: url("../image/burger-close.svg");
}
