.page-header__main-block {
    min-height: 80px;
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    background-color: #f8f8f8;
}

.page-header__logo-text {
    text-transform: uppercase;
    font-size: 18px;
}

.page-header__logo-bold {
    font-weight: 700;
}

.page-header__logo {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #222222;
}

.page-header__wrapper-navigation {
    display: flex;
    align-items: center;
    position: relative;
}

@media (min-width: @tablet-width) {
    .page-header__button-burger {
        display: none;
    }
}

@media(min-width: @desktop-width) {
    .page-header__main-block {
        padding: 0 34px;
    }
}

@media(min-width: 1440px) {
    .page-header__main-block {
        padding: 0 134px;
    }
}