.top-bar {
    background-color: #333333;
    min-height: 45px;
}

.top-bar__wrapper {
    display: flex;
    justify-content: space-between;
    min-height: 45px;
    align-items: center;
}

.top-bar__wrapper-select {
    margin-left: 20px;
    display: flex;
    align-items: center;
}

.top-bar__currency {
    font-size: 12px;
    font-weight: 500;
    color: #777777;
}

.top-bar__select {
    background-color: transparent;
    border: none;
    font-size: 12px;
    font-weight: 500;
    color: #777777; 
}

@media(min-width: @desktop-width) {
    .top-bar__wrapper {
        width: 1170px;
        margin: 0 auto;
    }

    .top-bar__wrapper-select {
        margin-left: 0;
    }
}