.banners {
    padding-bottom: 56px;
}

.banners__award {
    background-color: #333333;
    padding: 62px 30px 64px;
    font-size: 18px;
    color: #ffffff;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
}

@media(min-width: @tablet-width) {
    .banners {
        display: flex;
        justify-content: space-between;
    }

    .banners__award {
        width: 270px;
        font-size: 22px;
        margin-bottom: 0;
    }
}

@media(min-width: @desktop-width) {
    .banners__award {
        width: 453px;
        padding: 62px 58px 64px;
        font-size: 26px;
        margin-bottom: 0;
    }
}