.page-footer {
    background-color: #f8f8f8;
}

.page-footer__container {
    width: 280px;
    margin: 0 auto;
}

.page-footer__links {
    padding-top: 64px;
    & a {
        font-size: 11px;
        text-decoration: none;
        color: #969696;
    }

    & h2 {
        font-size: 11px;
        color: #777274;
        text-transform: uppercase;
        margin: 0;
        margin-bottom: 34px;
    }

    & li {
        font-size: 11px;

        &:not(:last-child) {
            margin-bottom: 19px;
        }

        &:hover a {
            color: #42d5d5;
        }
    }

    & section {
        margin-bottom: 60px;
    }
}

.page-footer__contact {
    & address {
        font-size: 11px;
        color: #777274;
        font-style: normal;
        margin-bottom: 29px;
    }

    & a {
        display: block;
        margin-bottom: 19px;

        &:hover {
            color: #42d5d5;
        }
    }

    & span.page-footer__contact-email {
        font-size: 11px;
        color: #777274;
    }
}

@media(min-width: @tablet-width) {
    .page-footer__container {
        width: 700px;
    }

    .page-footer__links {
        display: grid;
        grid-template-columns: 210px 210px 210px;
        column-gap: 35px;
        row-gap: 35px;
        margin-bottom: 65px;

        & section {
            margin-bottom: 0;
        }
    }
}

@media(min-width: @desktop-width) {
    .page-footer__container {
        width: 1170px;
    }

    .page-footer__links {
        grid-template-columns: 130px 135px 137px 148px 223px;
        column-gap: 99px;
        & section {
            margin-bottom: 0;
        }
    }
}