.banner {
    min-height: 450px;
    background-image: url("../image/banner.jpg");
    background-position: 50% 50%;
    margin-bottom: 32px;
}

.banner__shadow {
    width: 100%;
    min-height: 450px;
    background-color: #333333;
    opacity: 75%;   
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.banner__text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 2;
    text-transform: uppercase;
    letter-spacing: 2px;

    & h1 {
        font-weight: 700;
        font-size: 36px;
        color: #ffffff;
        margin: 0;
        margin-bottom: 54px;
        
        & span {
            font-weight: 300;
        }
    }
}

.banner__slogan {
    font-weight: 400;
    font-size: 20px;
    color: #ffffff;
}

.banner__background-text {
    display: none;
}

@media(min-width: @tablet-width) {
    .banner__slogan {
        font-size: 23px;
    }
}

@media(min-width: @desktop-width) {
    .banner {
        margin-bottom: 62px;
    }

    .banner__background-text {
        display: block;
        font-weight: 700;
        font-size: 450px;
        color:#3e3e3e;
        position: absolute;
        top: 13px;
        letter-spacing: 70px;
    }
}